@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap);

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    color: white;
}

body::-webkit-scrollbar {
    width: 0px;
}

.app {
    min-height: 98.5vh;
    border: 8px solid #39445a93;
    margin: 5px;
    padding: 5px;
    background-image: url(/React-Quiz-Application/static/media/bg.ab549a14.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.title {
    text-transform: uppercase;
    font-size: 5vw;
    font-family: "Montserrat", sans-serif;
    cursor: pointer;
    text-decoration: none;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.divider {
    margin: 10px;
    width: 100%;
    background-color: grey;
}
.content {
    display: flex;
    justify-content: space-around;
}

.settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
}

.banner {
    width: 55%;
    align-self: center;
    padding: 8px;
}

@media (max-width: 800px) {
    .content {
        flex-direction: column-reverse;
        align-items: center;
    }
    .banner {
        width: 85%;
        padding: 0;
        padding-top: 20px;
    }
    .settings {
        width: 100%;
    }
}

.settings_select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8 1;
    justify-content: space-evenly;
}

.MuiMenu-paper {
    background-color: black !important;
}

.inputbase {
    border: 2px solid white;
    border-radius: 5px;
    padding: 10px 10px;
}
.quiz {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins", sans-serif;
}

.subtitle {
    font-size: 25px;
    border: 1px solid white;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
}

.quizInfo {
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    margin: 10px;
}
.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.singleQuestion {
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid grey;
    padding: 20px;
    margin-top: 10px;
}

.options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
}

.singleOption {
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    box-shadow: 0 0 2px black;
    color: black;
}

@media (max-width: 614px) {
    .singleOption {
        width: 100%;
    }
}

.controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.select {
    background-color: rgb(7, 207, 0);
    color: white;
    box-shadow: 0 0 1px black;
}

.wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    box-shadow: 0 0 1px black;
}
.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
}
